<template>
    <div class="box">
        <header>
            <router-link :to="{name:'List'}">
                <img src="../../images/110.png" alt="">
            </router-link>
            <span>我的</span>
        </header>
        <div class="content">
            <div class="portrait">
                    <ul>
                        <!-- <li class="img"><img :src="'//elm.cangdu.org/img/'+imgs" ></li> -->
                        <li class="img">
                            <img :src="'https://elm.cangdu.org/img/' + headPhoto" alt="" cl>
                            <!-- <input id="result" ref="upload" @change="upload" type="file" style="opacity: 0" > -->
                        </li>
                        <!-- <li class="img"></li> -->
                        <li>
                        <router-link :to="{name:'Login'}" tag="p" v-if="this.$store.state.obj==''" class="p">登陆|注册</router-link>
                        <p v-else class="p">{{this.$store.state.obj.username}}</p>
                            <p>
                            <svg viewBox="0 0 655 1024" id="mobile" width="4%" height="4%" class="svg" >
                                <path fill="white" d="M0 122.501v778.998C0 968.946 55.189 1024 123.268 1024h408.824c68.52 0 123.268-54.846 123.268-122.501V122.501C655.36 55.054 600.171 0 
                                532.092 0H123.268C54.748 0 0 54.846 0 122.501zM327.68 942.08c-22.622 0-40.96-18.338-40.96-40.96s18.338-40.96 40.96-40.96 40.96 18.338 40.96 40.96-18.338 40.96-40.96 40.96zM81.92 163.84h491.52V819.2H81.92V163.84z">
                                </path></svg>
                                暂无绑定手机号</p>
                        </li>
                        <li>
                            <router-link :to="{name:'Tui'}">
                               <van-icon name="arrow" class="a"/>
                            </router-link>
                        </li>
                    </ul>
            </div>
            <div class="data">
                <ul>
                    <li>
                        <p><b class="mone">0.00</b>元</p>
                        <p>我的余额</p>
                    </li>
                    <li>
                        <p><b class="none">5</b>个</p>
                        <p>我的优惠</p>
                    </li>
                    <li>
                        <p><b class="cone">0</b>分</p>
                        <p>我的积分</p>
                    </li>
                </ul>
            </div>
            <div class="myorder">
                    <ul>
                        <li><van-icon class="van" name="bars"/></li>
                        <li>我的订单</li>
                        <li><van-icon name="arrow"/></li>
                    </ul>
                
                <ul>
                    <li><van-icon class="von" name="bag"/></li>
                    <li>积分商城</li>
                    <li><van-icon name="arrow"/></li>
                </ul>
                <ul>
                    <li><van-icon class="vcn" name="hot"/></li>
                    <li>饿了么会员卡</li>
                    <li><van-icon name="arrow"/></li>
                </ul>
            </div>
            <div class="order">
                <ul>
                    <li><van-icon class="one" name="smile"/></li>
                    <li>服务中心</li>
                    <li><van-icon name="arrow"/></li>
                </ul>
                <ul>
                    <li><van-icon name="http://img3.imgtn.bdimg.com/it/u=2249178182,289252315&fm=26&gp=0.jpg" /></li>
                    <li>下载饿了么APP</li>
                    <li><van-icon name="arrow"/></li>
                </ul>
                <!-- <div>{{this.n}}</div> -->
            </div>
        </div>
    </div>
</template>


<script>
    import axios from "axios"
    import { Icon } from 'vant';
    import {mapState,mapGetters,mapActions,mapMutations} from 'vuex'
    export default {
        data(){
            return{
                headPhoto:''
            }
        },
        mounted(){
            function  rem(){
                    document.documentElement.style.fontSize=document.documentElement.clientWidth/7.5+"px";
                }
                //onresize  当屏幕的尺寸发生改变的时候会触发的事件
                rem();
            window.onresize=rem;
            this.headPhoto = this.$store.state.img
            this.axios.get("https://elm.cangdu.org/v1/user?user_id="+this.user_id).then((res)=>{
                console.log(res)
                this.$store.state.username=res.data.username
            })
            this.$store.commit('gaiImg')
        }
    }
</script>


<style lang="less" scoped>
    .box{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        // font-size: 0.35rem;
        header{
            widows: 100%;
            height: 1rem;
            background: #3792E5;
            position: relative;
            img{
                width: 0.45rem;
                position: absolute;
                top: 0.2rem;
                left: 0.2rem;
            }
            span{
                display: block;
                position: absolute;
                top: 0.2rem;
                left: 3.3rem;
                color: white;
                font-size: 0.45rem;
            }
        }
    }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        background:  rgb(247, 239, 239);
        .portrait{
            width: 100%;
            ul{
                width: 100%;
                height: 2rem;
                background: rgb(240, 150, 220);
                display: flex;
                font-size: 0.3rem;
                .p{
                    margin-top: -0.2rem;
                    color: white;
                }
                .a{
                    color: white;
                }
                .img{
                    flex: 1;
                    margin: 0.25rem;
                    img{
                        width: 1.5rem;
                        border-radius:50%; 
                    }
                    .na{
                        width: 1.5rem;
                        border-radius:50%; 
                    }
                }
                li:nth-of-type(2){
                    margin: 0.45rem 0.1rem;
                    flex: 5;
                    color: white;
                    p{
                        padding-top: 0.1rem;
                    }
                }
                li:nth-of-type(3){
                    margin: 0.85rem 0;
                    color: white;
                    margin-left: 0.5rem;
                    flex: 1;
                }
            }
        }
    }
    .data{
        width: 100%;
        height: 1.9rem;
        background: white;
        border-bottom: 0.2rem solid rgb(247, 239, 239);
        ul{
            margin: 0;
            padding: 0;
            width: 100%;
            height: 1.7rem;
            display: flex;
            flex-wrap: wrap;
            text-align: center;
            li{
                width: 33%;
                font-size: 0.3rem;
                border-right: 0.001rem solid rgb(224, 199, 199);
                .mone{
                    font-size: 0.5rem;
                    color: greenyellow;
                }
                .none{
                    font-size: 0.5rem;
                    color: red;
                }
                .cone{
                    font-size: 0.5rem;
                    color:orange;
                }
                p{
                    // padding-top: 0.2rem;
                }
            }
            li:nth-of-type(3){
                border: none;
            }
        }
    }
    .myorder{
        width: 100%;
        background: white;
        border-bottom: 0.2rem solid rgb(247, 239, 239);
        ul{
            width: 100%;
            height: 1rem;
            display: flex;
            font-size: 0.3rem;
            margin-top: 0.3rem;
            margin-left: -0.2rem;
            li:nth-of-type(1){
                flex: 1;
                line-height: 1rem;
                margin-left: 0.6rem;
                margin-top: 0.04rem;
            }
            li:nth-of-type(2){
                flex: 10;
                line-height: 1rem;
            }
            li:nth-of-type(3){
                flex: 1;
                line-height: 1rem;
            }
            .van{
                font-size: 0.35rem;
                color: rgb(216, 192, 192);
            }
            .von{
                font-size: 0.35rem;
                color: rgb(201, 175, 129);
            }
            .vcn{
                font-size: 0.35rem;
                color: yellowgreen;
            }
        }
    }
    .order{
         width: 100%;
         background: white;
         ul{
            width: 100%;
            height: 1rem;
            display: flex;
            font-size: 0.3rem;
            margin-left: -0.2rem;
            li:nth-of-type(1){
                flex: 1;
                line-height: 1rem;
                margin-left: 0.6rem;
                margin-top: 0.04rem;
            }
            li:nth-of-type(2){
                flex: 10;
                line-height: 1rem;
            }
            li:nth-of-type(3){
                flex: 1;
                line-height: 1rem;
            }
         }
         .one{
             font-size: 0.35rem;
             color: rgb(173, 142, 202);
         }
    }
</style>


